	module.exports = {"data":{"markdownRemark":{"html":"<h1>ИГРУШКИ</h1>\n<p>Сердце в прошлое обуто —<br>\nя ударился об утро:<br>\nзажужжало за диваном<br>\nвсё, что детство задевало.</p>\n<p>Кличут первые игрушки,<br>\nговорят наоборот,<br>\nпомнят лампочки и груши<br>\nмой беззубый смелый рот.</p>\n<p>Если носок дырявый,<br>\nЗначит это червяк<br>\nвылез прямо из правой<br>\nноги плясать краковяк.</p>\n<p>Дерево — это ступеньки!<br>\nЛестницу — только в прыжок!<br>\nСодранные коленки<br>\nЯ стирал в порошок!</p>\n<p>Сердце в прошлое обуто —<br>\nя ударился об утро:<br>\nзажужжало за диваном<br>\nвсё, что детство задевало.</p>\n<p>Мудрый дедушка баюкал,<br>\nбыл всегда в очках и брюках.<br>\nВ пряже бабушка жила,<br>\nбудто это кожура.</p>\n<p>У двери росла засечка —<br>\nкаждый месяц по чуть-чуть —<br>\nподнимала человечка,<br>\nчтобы будущим обуть.</p>\n<p>Мама — это поцелуи<br>\nв руки, в локти, в лоб, в живот.<br>\nПапа — это тот, кто в клюве<br>\nдержит то, что не живёт.</p>\n<p>Мама всегда покупала<br>\nновые ложки в дом —<br>\nложка за маму стала<br>\nсамым строгим отцом!</p>\n<p>Правда всегда на стенах!<br>\nРуки не держат ложь!<br>\nС усами из мыльной пены<br>\nя на пирата похож!</p>\n<h3>БЛЮЗ КАПИТАНА ВЛАДА</h3>\n<p>Я пират<br>\nя закопал<br>\nклад<br>\nв ванне моей<br>\nя повелитель морей</p>\n<p>Я грозный пират<br>\nЯ Капитан<br>\n         Влад<br>\nя морской волк<br>\nа на руке крутая переводная наколка</p>\n<h3>***</h3>\n<p>Сердце в прошлое обуто —<br>\nя ударился об утро:<br>\nзажужжало за диваном<br>\nвсё, что детство задевало.</p>\n<p>Первобытные рисунки<br>\nспрятал я надёжно в сумке.<br>\nМоя мама — кенгуру,<br>\nэто значит, не умру.</p>\n<p>Это значит, сердце будто<br>\nбьётся головой об утро —<br>\nстрелки на часах идут.<br>\nЯ живой. Я есть. Я тут.</p>"}},"pathContext":{"slug":"lyrics/igrushki"}}

